import baseApi from './baseApi'

class CommentApi extends baseApi {
  constructor() {
    super();
    this.namespace = 'comment';
    this.endpoints = {
      comments: '{type}/{id}/comments',
    };

    this.after_init();
  }

  add(data) {
    /**
     * if prod_id is Object, get data from it;
     * @see Object.prototype.getElementSallaData
     */
    if (data?.data) {
      data = data.data;
    }
    data = this.getCommentPayload(data, true);

    if (typeof data === 'string') {
      salla.event.comment.additionFailed(data);
      return salla.api.errorPromise(data);
    }

    if (!data.comment) {
      let error = 'can\'t find comment content!'
      salla.event.comment.additionFailed(error);
      return salla.api.errorPromise(error);
    }

    return this.request(['comments', data.type, data.id], {comment: data.comment})
      .then(function (response) {
        salla.event.comment.added(response, data.id);
        return response;
      })
      .catch(function (error) {
        salla.event.comment.additionFailed(error, data.id);
        throw error;
      });
  }

  getCommentPayload(data) {

    let payload = {};
    ['id', 'type', 'comment', 'per_page', 'page'].forEach(key => {
      let value = salla.form.getPossibleValue(data, [key]);
      if (value) {
        payload[key] = value;
      }
    })


    if (!payload.id) {
      return 'Failed to get id!';
    }
    if (!payload.type || !['products', 'pages', 'product', 'page'].includes(payload.type)) {
      return 'Failed to get type one of:(products, product, page, pages)!';
    }

    //we need type in plural format to be injected into the url.
    payload.type += ['product', 'page'].includes(payload.type) ? 's' : '';

    return payload;
  }

  fetch(data) {
    data = this.getCommentPayload(data);
    if (typeof data === 'string') {
      salla.event.comment.fetchFailed(data);
      return salla.api.errorPromise(data);
    }

    //todo:: support pagination
    return this.request(['comments', data.type, data.id], {params: data}, 'get')
      .then(function (response) {
        salla.event.comment.fetched(response, data);
        return response;
      })
      .catch(function (error) {
        salla.event.comment.fetchFailed(error, data);
        throw error;
      });
  }

  getPageComments(pageId, page = 1, per_page = 10) {
    return this.fetch({id: pageId, type: 'pages', page: page, per_page: per_page});
  }

  getProductComments(productId, page = 1, per_page = 10) {
    return this.fetch({id: productId, type: 'products', page: page, per_page: per_page});
  }
}

export default CommentApi;
