import baseApi from './baseApi'

class LandingApi extends baseApi {
  constructor() {
    super();
    this.namespace = 'landing';
    this.endpoints = {
      getDetails        : 'landing/{landing_page_id}',
      createCartFromPage: 'landing/{landing_page_id}',//auth
    };
    //salla.sa/api/v1/landing/123527

    this.endpointsMethods = {
      getDetails: 'get',
    };

    this.after_init();
  }

  getDetails(landing_page_id) {
    if (!landing_page_id) {
      let message = 'page landing id not passed';
      salla.event.landing.getDetailsFailed(message);
      return salla.api.errorPromise(message);
    }

    return this.request(['getDetails', landing_page_id])
      .then(function (response) {
        salla.event.landing.detailsFetched(response, landing_page_id);
        return response;
      })
      .catch(function (error) {
        salla.event.landing.getDetailsFailed(error, landing_page_id);
        throw error;
      });
  }

  createCartFromPage(landing_page_id) {
    if (!landing_page_id) {
      let message = 'page landing id not passed';
      salla.event.landing.orderCreationFailed(message);
      return salla.api.errorPromise(message);
    }

    if (salla.config.isGuest()) {
      salla.auth.api.setAfterLoginEvent('landing::createCartFromPage', landing_page_id);
      salla.event.dispatch('login::open');
      return salla.api.successPromise('');
    }


    return this.request(['createCartFromPage', landing_page_id])
      .then(function (response) {
        salla.event.landing
          .orderCreated(response, landing_page_id)
          .then(() => location.href = response.data.url);
        return response;
      })
      .catch(function (error) {
        salla.event.landing.orderCreationFailed(error, landing_page_id);
        throw error;
      });
  }
}

export default LandingApi;
