import baseApi from './baseApi';

class ProductApi extends baseApi {
    constructor() {
        super();
        this.namespace = 'product';
        this.previousQuery = '';
        this.endpoints = {
            getPrice             : 'products/{id}/price',
            availabilitySubscribe: 'products/{id}/availability-notify',
            search               : 'products/search',
            details              : 'products/{product_id}/details',
            categories           : 'products/categories/{?id}',
            offers               : 'products/{product_id}/specialoffer',
            getSizeGuides        : 'products/{prod_id}/size-guides',
            giftDetail           : 'products/{product_id}/buy-as-gift',
            giftToCart           : 'products/{product_id}/buy-as-gift',
            giftImage            : 'products/buy-as-gift/image',
            buyNow               : 'products/{product_id}/buy-now',
            fetch                : 'products'
        };

        this.endpointsMethods = {
            details: 'get',
            giftDetail: 'get',
            fetch: 'get'
        }

        this.after_init();
    }

    /**
     * Getting Price Of Product
     * @param {int|Object|Object|FormData} productIdOrPayload
     * @return {Promise}
     */
    getPrice(productIdOrPayload) {
        let payload = productIdOrPayload.data || productIdOrPayload;
        let product_id = salla.form.getPossibleValue(payload, ['id', 'prod_id', 'product_id']);

        return this.request(['getPrice', product_id], typeof payload == 'object' ? payload : undefined)
            .then(function (response) {
                salla.product.event.priceUpdated(response, product_id);
                return response;
            })
            .catch(function (error) {
                salla.product.event.priceUpdateFailed(error, product_id);
                throw error;
            });
    }

    /**
     * @return {Promise}
     */
    categories(categoryId) {
        return this.request(['categories', categoryId || ''], null, 'get')
            .then(function (response) {
                salla.product.event.categoriesFetched(response);
                return response;
            })
            .catch(function (error) {
                salla.product.event.categoriesFailed(error);
                throw error;
            });
    }

    availabilitySubscribe(prodId) {
        let formData = prodId.data || prodId;
        prodId = salla.form.getPossibleValue(formData, ['id', 'prod_id']);

        return this.request(['availabilitySubscribe', prodId], typeof formData == 'object' ? formData : undefined)
            .then(function (response) {
                salla.product.event.availabilitySubscribed(response, prodId);
                return response;
            })
            .catch(function (error) {
                salla.product.event.availabilitySubscribeFailed(error, prodId);
                throw error;
            });
    }

    /**
     * @param {SearchPayload|string|any} query
     * @return {Promise<unknown>}
     */
    search(query) {
        /**
         * if query is Object, get data from it;
         * @see Object.prototype.getElementSallaData
         */
        let formData = query.data;
        if (!formData) {
            formData = {
                params: typeof query === 'string' ? { query: query } : query
            };
        }
        query = formData instanceof FormData ? formData.get('query') : (formData.query || formData.params?.query);

        if (!query) {
            let msg = 'There is no "query"!';
            salla.product.event.searchFailed(msg);
            return salla.api.errorPromise(msg);
        }

        if (query === salla.api.product.previousQuery) {
            let msg = 'Query is same as previous one!';
            salla.product.event.searchFailed(msg);
            salla.api.product.previousQuery = null;
            return salla.api.errorPromise(msg);
        }
        salla.api.product.previousQuery = query;

        return this.request('search', formData, 'get')
            .then(function (response) {
                salla.product.event.searchResults(response, query);
                return response;
            })
            .catch(function (error) {
                salla.product.event.searchFailed(error, query);
                throw error;
            });
    }

    /**
     * Show offer popup for this product
     * @param {Object|number} product_id
     */
    offers(product_id) {
        product_id = salla.form.getPossibleValue(product_id?.data | product_id, ['product_id', 'id']);
        if (!product_id) {
            let message = 'There is no "product_id"!';
            salla.product.event.fetchOffersFailed(message);
            return salla.api.errorPromise(message);
        }

        return this.request(['offers', product_id])
            .then(function (response) {
                salla.product.event.offersFetched(response, product_id);
                return response;
            })
            .catch(function (error) {
                salla.product.event.fetchOffersFailed(error, product_id);
                throw error;
            });
    }

    /**
     * Get size guides
     *
     * The product id for which the size guide is required.
     * @param {string} prod_id
     */
    getSizeGuides(prod_id) {
        return this.request(`products/${prod_id}/size-guides`, null, 'get')
            .then(response => {
                salla.product.event.sizeGuideFetched(response, prod_id)
                return response;
            })
            .catch((err) => {
                salla.product.event.sizeGuideFetchFailed(err, prod_id)
                throw err;
            });
    }

    /**
    * Get gifting system
    *
    * The product id for which the gifting system is required.
    * @param {number} product_id
    */
    getGiftDetails(product_id) {
        return this.request(['giftDetail', product_id])
            .then(resp => {
                salla.product.event.giftFetched(resp, product_id)
                return resp;
            }).catch((err) => {
                salla.product.event.giftFetchFailed(err, product_id)
                throw err;
            });
    }

    /**
     * Get Product details by product ID
     * @param {number} productId
     * @param {Array} withItems
     */
    getDetails(productId, withItems = []) {
        if (!Array.isArray(withItems)) {
            let msg = 'withItems should be array.';
            salla.product.event.detailFetchFailed(msg)
            return salla.api.errorPromise(msg);
        }

        if (!productId) {
            let msg = 'productId is not passed.';
            salla.product.event.detailFetchFailed(msg)
            return salla.api.errorPromise(msg);
        }

        return this.request(['details', productId], { params: { with: withItems } })
            .then(function (response) {
                salla.product.event.detailFetched(response, productId)
                return response;
            })
            .catch(function (error) {
                salla.product.event.availabilitySubscribeFailed(error, productId)
                throw error;
            });
    }

    /**
    * Add gift to cart
    *
    * The product id for which the gifting system is required.
    * @param {number} product_id
    * @param {Object} payload
    * @param {Object} withRedirect
    * @return {Promise<unknown>|*}
    */
    addGiftToCart(product_id, payload, withRedirect = false) {
        return this.request(['giftToCart', product_id], payload)
            .then(resp => {
                salla.product.event.addGiftToCartSucceeded(resp, product_id)
                if (withRedirect) {
                    window.location.href = resp.redirect
                }
                return resp;
            }).catch((err) => {
                salla.product.event.addGiftToCartFailed(err, product_id)
                throw err;
            });
    }

    /**
     * Upload Custom Image for gift
     * @param {FormData} multipartPayload
     */
    uploadGiftImage(multipartPayload) {
        return this.request('giftImage', multipartPayload)
            .then(resp => {
                salla.product.event.giftImageUploadSucceeded(resp)
                return resp;
            }).catch(e => {
                salla.product.event.giftImageUploadFailed(e);
                throw e;
            });
    }

    /**
     * Source
     * The source of the products list
     * - 'categories' | 'latest' | 'related' | 'brands' | 'json' | 'search' | 'tags' | 'selected' | 'offers' | 'landing-page'
     *
     * Source Value
     * The source value, cloud be different values as following:
     * - array of ids when `source` in ['categories', 'brands', 'tags', 'selected']
     * - keyword when `source` = 'search'
     * - products payload when `source` = 'json'
     * - product_id when `source` = 'related'
     *
     * Limit
     * Limit for number of products in the list.
     * - number
     *
     * Filters
     * Filter parameters.
     * - JSON STRING or object
     * @param {ProductRequest.FetchProductsQueryParams} queryParams
     * @return {Promise<ProductResponse.lists>}
     */
    fetch(queryParams) {
        if (!queryParams.source) {
            let msg = 'Source can not be empty. Please provide one.';
            salla.product.event.productListFetchFailed(msg)
            return salla.api.errorPromise(msg);
        }

        if ((['categories', 'brands', 'tags', 'selected'].includes(queryParams.source) && !Array.isArray(queryParams.source_value) 
            || (queryParams.source === 'search' && typeof queryParams.source_value !== 'string')) 
            || (queryParams.source === 'related' && typeof queryParams.source_value !== 'number'))
            {
            let msg = 'SourceValue type error.';
            salla.product.event.productListFetchFailed(msg)
            return salla.api.errorPromise(msg);
        }

        return this.request(this.endpoints.fetch, {params: queryParams}, 'get').then(resp => {
            salla.product.event.productListFetchSucceeded(resp)
            return resp;
        }).catch(e => {
            salla.product.event.productListFetchFailed(e);
            throw e;
        });
    }

    // buyNow(productId, payload){
    //     if(isNaN(productId)){
    //         let message='Product id looks not a valid product number';
    //         salla.product.event.buyNowFailed(message);
    //         return salla.api.errorPromise(message);
    //     }
    //     return this.request(['buyNow', productId], payload)
    //       .then(resp => {
    //           salla.product.event.buyNowSucceeded(resp);
    //           if (resp.data.redirect) {
    //               window.location.href = resp.data.redirect;
    //           }
    //           return resp;
    //       }).catch(e => {
    //           salla.product.event.buyNowFailed(e);
    //           throw e;
    //       });
    // }
}

export default ProductApi;
