import baseApi from './baseApi';

class BookingApi extends baseApi {
  constructor() {
    super();
    this.namespace = 'booking';
    this.endpoints = {
      add: 'cart/booking/product/{id}',
    };

    this.endpointsMethods = {
      add: 'get',
    }
  }

  /**
   *
   * @param {Number} productId
   * @param {boolean} withRedirect
   */
  async add(productId, withRedirect = true) {
    if (!productId) {
      let message =  "Please provide product id.";
      salla.booking.event.additionFailed(message);
      return salla.api.errorPromise(message);
    }

    return this.request(['add', productId])
      .then(function (response) {
        salla.booking.event.added(response);
        if (withRedirect && response.data.redirect.to === 'booking') {
          window.location.href = response.data.redirect.url
        }
        return response;
      })
      .catch(function (error) {
        salla.booking.event.additionFailed(error);
        throw error;
      });
  }
}

export default BookingApi;