import Lang from 'lang.js'

export default class SallaLang extends Lang {
    constructor(options) {
        options = options || {};

        // TODO:: allow the developer to pass initiate values to avoid this message even when he will add translations later.
        options.messages = options.messages || window.translations;
        options.locale = options.locale || (window.locale || navigator.language || navigator.userLanguage || 'ar').split('-')[0];
        options.fallback = options.fallback || options.locale;

        super(options);
        this.translationsLoaded = false; // handy, when we are trying to run onLoaded(), after is already loaded.
    }

    onLoaded(callback) {
        if (this.translationsLoaded) {
            return callback();
        }

        // todo:: in case there is need support Salla.event.on() for synced translations
        Salla.event.once('languages::translations.loaded', callback);
    }

    loadStoreTranslations() {
        this.hash = salla.config.get('theme.translations_hash', salla.config.get('store.id', 'twilight'));
        //lets be smart in storage this request live, thanks a lot for the boss for this idea🤩
        if (!this.message) {
            salla.log('try to load translations from localstorage');
            this.messages = salla.storage.get(`s_translations.${this.hash}`);
            this.messages && (this.translationsLoaded = true) && super.setMessages(this.messages);
        }
        if (this.messages) {
            salla.event.dispatch('languages::translations.loaded');
            salla.logger.info('The messages of transactions is already loaded');
            return;
        }

        let url = salla.url.get('');
        if (!url) {
            this.loadScript('https://cdn.salla.network/js/translations.js', false);
            return;
        }

        // load default translations for salla, or using global translations file
        this.loadScript(salla.url.get(`languages/assets/${this.hash}.js`));
    }

    setMessages(translations) {
        super.setMessages(translations);
        salla.event.dispatch('languages::translations.loaded');
        this.translationsLoaded = true;
    }

    loadScript(url, withFallback = true) {
        let script = document.createElement('script');
        script.src = url;
        script.onload = () => {
            if (window.translations) {
                //if translations is updated by merchant, lets remove the old one if existed
                salla.storage.remove('s_translations');
                salla.storage.set('s_translations', {[this.hash]: window.translations});
                return this.setMessages(window.translations);
            }

            script.onerror();
        };
        script.onerror = () => {

            if (withFallback) {
                salla.logger.warn("Failed to load Translations for store, lets try load it from CDN");
                return this.loadScript('https://cdn.salla.network/js/translations.js', false);
            }

            salla.logger.error("Failed to load Translations, check your network logs for more details"
                + "\nor: salla.lang.setMessages({....}), see https://docs.salla.dev for more information's."
            );
        }
        document.head.appendChild(script);
    }

    get(key, replacements, locale) {
        let fullKey = key;
        //if translations in window object, means it's using lang.js formatting, so lets ignore first level key.
        //todo:: check why we need this condition🤔
        if (window.translations || this.translationsLoaded) {
            fullKey = 'trans.' + key;
        }

        let translated = super.get(fullKey, replacements, locale);
        if (translated !== fullKey) {
            return translated;
        }

        return super.get(fullKey, replacements, 'en').replace(fullKey, key);
    }

    set(key, message) {
        salla.helpers.setNested(this.messages[this.getLocale() + '.trans'], key, message);
        return this;
    }
}