import baseApi from './baseApi';

class CartApi extends baseApi {
    constructor() {
        super();
        this.namespace = 'cart';
        this.endpoints = {
            latest: 'cart/latest',
            details: 'cart/{cart}',
            // summary    : 'cart/{cart}/summary',
            quickAdd: 'cart/{cart}/item/{product}/quick-add',
            addItem: 'cart/{cart}/item/{product}/add',
            deleteItem: 'cart/{cart}/item/{item}',
            updateItem: 'cart/{cart}/item/{item}',
            deleteImage: 'cart/{cart}/image/{image}',
            uploadImage: 'cart/{cart}/image',
            status: 'cart/{cart}/status',
            // submit     : 'cart/{cart}',
            // uploadOptionImage: 'cart/{cart}/option-image',
            addCoupon: 'cart/{id}/coupon',
            deleteCoupon: 'cart/{id}/coupon',
            getQuickOrderSettings: 'checkout/quick-order',
            createQuickOrder: 'checkout/quick-order',
            priceQuote: "cart/{cartId}/price-quote"
        };
        this.endpointsMethods = {
            latest: 'get',
            details: 'get',
            // summary    : 'get',
            status: 'get',
            updateItem: 'post',
            deleteItem: 'delete',
            deleteImage: 'delete',
            deleteCoupon: 'put',
            getQuickOrderSettings: 'get',
        };
        this.webEndpoints = [
            'latest',//it's important to be web for getting cart by session when user not logged in
        ];

        this.latestCart = null;

        this.after_init();
        /**
         * we need to get the latest cart, if currentCart is not set in the localstorage,
         * using @method `this.getCurrentCartId()` will do it for us😉
         */
        salla.event.on('api::initiated',  ()=> this.getCurrentCartId());
    }

    async getCurrentCartId() {
        if (salla.cart.api.latestCart) {
            return salla.cart.api.latestCart.cart.id;
        }

        let cartId = salla.storage.get('cart.id');
        if (cartId) {
            return cartId;
        }

        let cart = await this.latest();

        salla.cart.api.latestCart = cart.data;

        return salla.cart.api.latestCart.cart.id;
    }

    getUploadImageEndpoint(cart_id) {
        return salla.url.api(this.getUrl('uploadImage', cart_id || salla.storage.get('cart.id')));
    }

    latest() {
        return this.request('latest', { params: { source: '' } })
            .then(res => {
                salla.storage.set('cart', res.data.cart);
                salla.event.cart.latestFetched(res);
                return res;
            })
            .catch(error => {
                salla.storage.set('cart', '');
                salla.event.cart.latestFailed(error);
                throw error;
            });
    }

    /**
     * @param {number|null} cartId
     * @param {['options' | 'attachments']} withItems
     * @return {Promise<any>}
     */
    async details(cartId, withItems = []) {
        cartId = cartId || await this.getCurrentCartId();
        return this.request(['details', cartId], {params: {with: withItems}})
          .then(function (response) {
              salla.cart.event.detailsFetched(response);
              return response;
          })
          .catch(function (error) {
              salla.cart.event.detailsNotFetched(error);
              throw error;
          });
    }

    /**
     * An Alias For addItem, But For QuickAdd Only.
     *
     * todo:: drop it is not used
     * @param {int} prodId
     * @param quantity
     * @return {Promise}
     */
    async quickAdd(prodId, quantity) {
        return this.addItem({ id: prodId, quantity: quantity, endpoint: 'quickAdd' });
    }

    /**
     * Add item to cart
     * 💡 If Quantity not passed will use 'quickAdd' endpoint.
     *
     * @param {int|DataFromHook|FormData|Object} productIdOrObject
     * @return {Promise<unknown>|boolean}
     */
    async addItem(productIdOrObject) {
        let data = this.getCartPayload(productIdOrObject);

        if (!data.id) {
            let message = 'There is no product "id"!';
            salla.cart.event.itemAddedFailed(message);
            return salla.api.errorPromise(message);
        }

        //to force using quickAdd endpoint, pass argument [endpoint] to the payload
        let endpoint = salla.form.getPossibleValue(data.payload, ['endpoint']);
        if (!endpoint || !['addItem', 'quickAdd'].includes(endpoint)) {
            //if quantity is existed, change endpoint & add quantity to formData.
            endpoint = salla.form.getPossibleValue(data.payload, ['quantity', 'donating_amount'])
                ? 'addItem'
                : 'quickAdd';
        }

        let cartId = await this.getCurrentCartId();


        return this.request([endpoint, cartId, data.id], data.payload)
            .then(function (response) {
                salla.cart.event.itemAdded(response, data.id);
                return response;
            })
            .catch(function (error) {
                salla.cart.event.itemAddedFailed(error, data.id);
                throw error;
            });
    }

    async deleteItem(item_id) {
        let data = this.getCartPayload(item_id);
        if (!data.id) {
            let message = 'There is no "id"!';
            salla.cart.event.itemDeletedFailed(message);
            return salla.api.errorPromise(message);
        }
        let cartId = await this.getCurrentCartId();

        return this.request(['deleteItem', cartId, data.id])
            .then(function (response) {
                salla.cart.event.itemDeleted(response, data.id);
                return response;
            })
            .catch(function (error) {
                salla.cart.event.itemDeletedFailed(error, data.id);
                throw error;
            });
    }

    async updateItem(item_id) {
        let data = this.getCartPayload(item_id);
        if (!data.id) {
            let message = 'There is no "id"!';
            salla.cart.event.itemUpdatedFailed(message);
            return salla.api.errorPromise(message);
        }
        let cartId = await this.getCurrentCartId();

        if (data.payload.constructor?.name === 'Object') {
            data.payload['_method'] = 'PUT';
        } else {
            data.payload.append('_method', 'PUT');
        }

        /**
         * Lets us POST to support form data
         * this isn't a symfony (or laravel, or any other framework) issue, it's a limitation of PHP.
         */
        return this.request(['updateItem', cartId, data.id], data.payload)
            .then(function (response) {
                salla.cart.event.itemUpdated(response, data.id);
                return response;
            })
            .catch(function (error) {
                salla.cart.event.itemUpdatedFailed(error, data.id);
                throw error;
            });
    }

    async deleteImage(fileId) {
        if (!(fileId = salla.form.getPossibleValue(fileId, ['id', 'image_id', 'photo_id']))) {
            let message = 'There is no "id"!';
            salla.cart.event.imageNotDeleted(message);
            return salla.api.errorPromise(message);
        }
        let cartId = await this.getCurrentCartId();

        return this.request(['deleteImage', cartId, fileId])
            .then(function (response) {
                salla.cart.event.imageDeleted(response, fileId);
                return response;
            })
            .catch(function (error) {
                salla.cart.event.imageNotDeleted(error, fileId);
                throw error;
            });
    }

    async status(cartId) {
        return this.request(['status', cartId || await this.getCurrentCartId()], { params: { has_apple_pay: !!window.ApplePaySession } });
    }

    async submit() {
        this.status(await this.getCurrentCartId())
            .then(res => {
                let next_step = res.data.next_step.to;
                if (!['checkout', 'refresh', 'login'].includes(next_step)) {
                    let message = "Can't find next_step );";
                    salla.cart.event.submitFailed(message);
                    throw message;
                }
                salla.cart.event.submitted(res);
                if (next_step === 'login') {
                    salla.auth.setCanRedirect(false);
                    // next_step is login
                    salla.event.dispatch('login::open');
                    //todo:: make sure this will not be fired more than once & it will not be infinite loop
                    salla.auth.event.onLoggedIn(response => {
                        salla.event.dispatch('login::close');
                        this.submit();
                    });
                    return;
                }

                if (next_step === 'checkout') {
                    window.location.href = res.data.next_step.url + (window.ApplePaySession ? '?has_apple_pay=true' : '');
                } else {//next_step === 'refresh'
                    window.location.reload();
                }
            })
            .catch(function (error) {
                salla.cart.event.submitFailed(error);
                throw error;
            });
    }

    /**
     * Extract ProductId & formData from Passed Object
     * @param {number|Object} productIdOrObject
     * @return {{payload: *, id: number}}
     */
    getCartPayload(productIdOrObject) {
        /**
         * if productIdOrObject is Object, get data from it;
         * @see Object.prototype.getElementSallaData
         */
        let payload = productIdOrObject?.data || (typeof productIdOrObject == 'object' ? productIdOrObject : undefined);

        //try to get productIdOrObject from payload if it's existed
        productIdOrObject = payload
            ? salla.form.getPossibleValue(payload, ['prod_id', 'product_id', 'item_id', 'id'])
            : productIdOrObject;

        payload = typeof payload == 'object' ? payload : undefined;

        return {
            id: productIdOrObject,
            payload: payload,
        };
    }

    normalRequest(endPoint, formData = undefined, method = null) {
        return super.normalRequest(endPoint, formData, method).catch((error) => {
            // it Looks that cart id not valid🤔
            if (error?.response?.status === 403) {
                salla.cart.api.reset();
                salla.error(salla.lang.get('pages.checkout.try_again'));
            }

            throw error;
        });
    }

    reset() {
        salla.api.cart.latestCart = null;
        salla.storage.remove('cart');
        salla.cart.event.successReset();
    }


    //Coupons
    /**
     * Add Coupon to cart*
     * @param {string|Object|FormData|Object} coupon
     */
    async addCoupon(coupon) {
        coupon = coupon.data || coupon;

        if (!(coupon = salla.form.getPossibleValue(coupon, ['coupon']))) {
            let error = new Error('There is no "Coupon Code"!');
            error.name = 'EmptyCoupon';
            salla.event.cart.couponAdditionFailed(error);
            return salla.api.errorPromise(error);
        }
        let cartId = await salla.cart.api.getCurrentCartId();

        return this.request(['addCoupon', cartId], { coupon: coupon })
            .then(function (response) {
                salla.event.cart.couponAdded(response, cartId);
                return response;
            })
            .catch(function (error) {
                salla.event.cart.couponAdditionFailed(error, cartId);
                throw error;
            });
    }

    async deleteCoupon() {
        let cartId = await salla.cart.api.getCurrentCartId();
        return this.request(['deleteCoupon', cartId], {})
            .then(function (response) {
                salla.event.cart.couponDeleted(response, cartId);
                return response;
            })
            .catch(function (error) {
                salla.event.cart.couponDeletionFailed(error, cartId);
                throw error;
            });
    }

    /**
     *
     */
    getQuickOrderSettings() {
        return this.request('getQuickOrderSettings')
            .then(res => {
                salla.event.cart.quickOrderSettingFetched(res);
                return res;
            })
            .catch(error => {
                salla.event.cart.quickOrderSettingFailed(error);
                throw error;
            });
    }

    /**
     *
     * @param {quickOrderPayload} payload
     */
    createQuickOrder(payload) {
        return this.request('createQuickOrder', payload)
            .then((response) => {
                salla.event.cart.quickOrderSucceeded(response);
                return response;
            })
            .catch((error) => {
                salla.event.cart.quickOrderFailed(error);
                throw error;
            });
    }

    /**
     *
     * @param {number} cartId
     */
    async priceQuote(cartId) {
        if (salla.config.isGuest()) {
            salla.auth.api.setAfterLoginEvent('cart::priceQuote', cartId);
            salla.event.dispatch('login::open');
            return;
        }

        return this.request(['priceQuote', cartId || await this.getCurrentCartId()])
          .then((response) => {
              salla.cart.api.reset();
              return salla.event.cart.priceQuoteSucceeded(response)
                .then(() => {
                    //go to home same as submit reload, after one second, to allow success message to be shown.
                    setTimeout(()=>window.location.href = salla.url.get('/'),1000);

                    //show success message from backend
                    return response
                });
          })
          .catch((error) => {
              return salla.event.cart.priceQuoteFailed(error)
                .then(() => {
                    error.error?.code === 404 && window.location.reload();
                    throw error;
                });
          });
    }
}

export default CartApi;
