"use strict";

import Salla from "@salla.sa/base"

// Main Salla Elements
import Notify from './lib/notify';
import Module from './lib/module';

// Helpers & Plugins
import './helpers';
import './event';
import "./lib/config";
import InfiniteScroll from './lib/infiniteScroll';
import Form from './lib/form';
import SallaLang from "./lib/lang";
import Api from "./api";
import Init from "./lib/init";

Salla.status = 'loading';
Salla.notify = Notify;
Salla.lang = new SallaLang;
Salla.form = new Form;
Salla.infiniteScroll = new InfiniteScroll(undefined, salla.event.infiniteScroll);
Salla.api = new Api();

// alies access to the module have API & Event class
[
  'cart',
  'auth',
  'order',
  'scope',
  'rating',
  'comment',
  'loyalty',
  'product',
  'profile',
  'currency',
  'document',
  'wishlist',
  'booking',
  'landing'
].forEach(action => Salla[action] = new Module(Salla.api[action], Salla.event[action]))

Salla.call = action => {
  let object = Salla;
  let arr = action.split('.');
  while (arr.length && (object = object[arr.shift()])) ;
  return object;
};

// alias for quick access
Salla.init = config => new Init(config);
salla.event.once('twilight::init', salla.init);
//we pass some events after api request, this will fire them
//ex: profile::verification
salla.event.once('twilight::api', payload => {
  let events = payload?.events;
  events && salla.event.dispatchEvents(events);
});

Salla.success = Salla.notify.success;
Salla.error = Salla.notify.error;
Salla.versions.twilight = '[VI]{version}[/VI]';

/**
 *
 * @param callback
 * @return {TwilightEmitter}
 * @deprecated
 */
Salla.onInitiated = callback => salla.event.once('twilight::initiated', callback);
Salla.onReady = Salla.onReady || function (callback) {
  if (salla.status === 'ready') {
    callback(salla.config.all());
    return;
  }
  Salla.onInitiated(callback);
};

/**
 * this event to allow fire events from back end, we need it, because we will fire salla.init using event from backend,
 * @see https://github.com/SallaApp/Store/blob/master/Modules/Themes/Helpers/TwilightEvent.php#L20
 * @see https://github.com/SallaApp/Dashboard-Tracking/blob/master/src/Widgets/TrackingWidget.php#L22
 */
window.dispatchEvent(new CustomEvent('salla::created'));

export default Salla;
