import baseApi from './baseApi'

class ProfileApi extends baseApi {
  constructor() {
    super();
    this.namespace = 'profile';
    this.endpoints = {
      info          : 'auth/user',
      update        : 'profile/update',
      updateContacts: 'profile/contacts/update',
      updateSettings: 'profile/settings',
      verify        : 'profile/verify',
      delete        : 'profile'
    };

    this.endpointsMethods = {
      delete: 'delete',
    }

    this.after_init();
  }

  info() {
    return this.request('info', null, 'get')
      .then(response => {
        let data = {
          id            : response.data.id,
          type          : 'user',
          email         : response.data.email,
          mobile        : response.data.phone.code + response.data.phone.number,
          country_code  : response.data.phone.country,
          language_code : response.data.language,
          currency_code : response.data.currency,
          notifications : response.data.notifications,
          pending_orders: response.data.pending_orders,
          avatar        : response.data.avatar,
          first_name    : response.data.first_name,
          last_name     : response.data.last_name,

          //inject it for inner usage only, see s-user-menu
          fetched_at: Date.now(),
        };
        salla.config.set('user', data);
        salla.storage.set('user', data)
        salla.profile.event.infoFetched(response);
        return response;
      })
      .catch(error => {
        salla.profile.event.infoNotFetched(error);
        throw error;
      })
  }

  /**
   * @param {ProfileUpdatePayload} data
   * @return {Promise<ProfileResponse>}
   */
  update(data) {
    return this.request('update', data)
      .then(response => {
        salla.profile.event.updated(response);
        return response;
      })
      .catch(error => {
        salla.event.profile.updateFailed(error);
        throw error;
      })
  }

  /**
   * @param {ProfileUpdateContactPayload} data
   * @return {Promise<ProfileResponse>}
   */
  updateContacts(data) {
    return this.request('updateContacts', data)
      .then(response => {
        salla.profile.event.verificationCodeSent(response);
        return response;
      })
      .catch(error => {
        salla.event.profile.updateContactsFailed(error);
        throw error;
      })
  }

  /**
   * @param {ProfileUpdateContactPayload} data
   * @return {Promise<ProfileResponse>}
   */
  verify(data) {
    return this.request('verify', data)
      .then(response => {
        salla.profile.event.verified(response);
        return response;
      })
      .catch(error => {
        salla.event.profile.unverified(error);
        throw error;
      })
  }

  /**
   * @param {NotificationSettingPayload} payload
   * @return {Promise<SuccessResponse>}
   */
  updateSettings(payload) {
    return this.request('updateSettings', payload)
      .then(response => {
        salla.event.profile.settingsUpdated(response);
        return response;
      })
      .catch(error => {
        salla.event.profile.updateSettingsFailed(error);
        throw error;
      })
  }

  /**
   * @return {Promise<SuccessResponse>}
   */
  delete() {
    return this.request('delete')
      .then(response => {
        salla.storage.clearAll();
        salla.cookie.clearAll();
        salla.event.profile.deleted(response);
        window.location.href = salla.url.get('logout');
        return response;
      })
      .catch(error => {
        salla.event.profile.notDeleted(error);
        throw error;
      })
  }
}

export default ProfileApi;
